import Vue from 'vue'
import Vuex from 'vuex'
import firebase from '../firebase'
import router from '../router'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

const store = new Vuex.Store({
  strict: true,
  plugins: [createPersistedState()],
  state: {
    user: {
      displayName: null,
      photoURL: null,
      email: null
    },
    connected: false,
    users: [],
    messages: [],
    isTyping: false
  },
  actions: {
    signUp ({commit}, authData) {
      const email = authData.email
      const password = authData.password
      firebase.auth().createUserWithEmailAndPassword(email, password)
        .then(() => alert('You account has been created'))
        .catch(error => alert(error.message))
    },
    signIn ({commit}) {
      const provider = new firebase.auth.GoogleAuthProvider()
      firebase.auth().signInWithPopup(provider)
        .then(data => {
          commit('loggedIn', data.user)
          router.replace('/')
          this._vm.$socket.emit('add user', {
            displayName: data.user.displayName,
            photoURL: data.user.photoURL,
            email: data.user.email
          })
        })
        .catch(error => console.log(error))
    },
    signOut ({commit}) {
      firebase.auth().signOut().then(() => {
        this._vm.$socket.emit('user left', this.state.user)
        commit('signOut')
        router.replace('/login')
      })
    },
    sendMessage ({commit}, message) {
      console.log('new message action', message)
      this._vm.$socket.emit('newMessage', message)
    },
    isTyping ({commit}, status) {
      commit('isTyping', status)
      this._vm.$socket.emit('typing')
    }
  },
  mutations: {
    loggedIn (state, user) {
      state.user.displayName = user.displayName
      state.user.photoURL = user.photoURL
      state.user.email = user.email
    },
    signOut (state) {
      state.user.displayName = null
      state.user.photoURL = null
      state.user.email = null
    },
    isTyping (state, status) {
      state.isTyping = status
    },
    SOCKET_CONNECT (state) {
      state.connected = true
      console.log('socket.io connected')
    },
    SOCKET_DISCONNECT (state, data) {
      state.connected = false
      console.log('socket.io disconnected', data)
    },
    SOCKET_NEW_MESSAGE (state, message) {
      // Getting an array back from the server, grabbing first element
      state.messages.push(message[0])
    },
    SOCKET_LOGIN (state, data) {
      state.users = data[0].clients
    }
  },
  getters: {
    user (state) {
      return state.user
    },
    isAuthenticated (state) {
      return state.user.email !== null
    },
    users (state) {
      return state.users
    },
    messages (state) {
      return state.messages
    }
  }
})

export default store



